export default "html {\n  font-size: 14px;\n\n  @media (min-width: 768px) {\n    font-size: 16px;\n  }\n}\n\n.nav-link {\n  padding: 0;\n}\n\n.nav-item a {\n  padding: 0 0.5rem;\n}\n\na.navbar-brand {\n  white-space: normal;\n  text-align: center;\n  word-break: break-all;\n}\n\n.box-shadow {\n  box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.05);\n}\n\n.error {\n  color: red;\n  font-weight: bold;\n}\n\n$tile-height: 70px;\n$tile-width: 70px;\n$tile-gap: 10px;\n\n.tiles {\n  flex: 1;\n  position: relative;\n  display: grid;\n  grid-template-columns: repeat(auto-fit, 70px);\n  grid-template-rows: repeat(auto-fit, 70px);\n  grid-gap: 10px;\n  overflow: visible;\n\n  .tile {\n    display: flex;\n    position: relative;\n\n    @for $i from 1 through 8 {\n      &.cols-#{$i} {\n        grid-column: span $i;\n        width: $i * $tile-width + ($i - 1) * $tile-gap;\n      }\n\n      &.rows-#{$i} {\n        grid-row: span $i;\n        height: $i * $tile-width + ($i - 1) * $tile-gap;\n      }\n    }\n\n    &:hover {\n      outline: rgba(29, 29, 29, 0.1) solid 4px;\n    }\n\n    .teaser {\n      font-size: 1.2em;\n      padding: 1em;\n      background: #f8f9fa;\n    }\n  }\n}\n\n.notifications {\n  position: fixed;\n  display: flex;\n  align-items: flex-end;\n  flex-direction: column;\n  right: 2em;\n  bottom: 2em;\n}\n\n.notification-toast {\n  box-sizing: border-box;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  text-align: left;\n  padding: 0.8em;\n  background-color: #fff;\n  max-width: 500px;\n  position: relative;\n  box-shadow: 1px 7px 14px -5px rgba(0, 0, 0, 0.2);\n\n  & + & {\n    margin-top: 1em;\n  }\n\n  &.success:before {\n    background-color: #2bde3f;\n  }\n\n  &.error:before {\n    background-color: #e7431a;\n  }\n\n  &.info:before {\n    background-color: #1d72f3;\n  }\n\n  &.warning:before {\n    background-color: #ffc007;\n  }\n\n  &:before {\n    content: \"\";\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    