  display: none;\n    }\n    li {\n      display: block;\n    }\n    .col {\n      flex-basis: 100%;\n      display: flex;\n      padding: 10px 0;\n      &:before {\n        color: #6c7a89;\n        padding-right: 10px;\n        content: attr(data-label);\n        flex-basis: 50%;\n        text-align: right;\n      }\n    }\n  }\n}\n\n.dashboard-container {\n  max-width: 1000px;\n  margin: 0 auto;\n  padding: 20px;\n  /* Add more styling as needed */\n}\n";